import { Component } from '@angular/core';
import { AlertController, Events, IonicPage, ModalController, NavController, NavParams } from 'ionic-angular';
import { Calendar } from '@ionic-native/calendar';
import { AddEventPage, EvolutionsPage, HospitalMonitoringPage, PatientHouseholdMonitoringPage } from '../index';
import { SchedulesProvider } from '../../providers/schedules/schedules';
import { ScheduleMapper } from '../../models/schedule.model';
import { User } from '../../providers';
import { UserMapper } from '../../models/user.model';
import { __USER_LOGGED } from '../../app/app.component';
import { AnalytictsTrack } from '../../decorators/analyticts-track.decorators';

/**
 * Generated class for the SchedulesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@AnalytictsTrack('[AGENDAMENTO] Tela de agendamentos')
@IonicPage({
  name: 'SchedulesPage'
})
@Component({
  selector: 'page-schedules',
  templateUrl: 'schedules.html',
})
export class SchedulesPage {

  date: any;
  daysInThisMonth: any;
  daysInLastMonth: any;
  daysInNextMonth: any;
  monthNames: string[];
  currentMonth: any;
  currentYear: any;
  currentDate: any;
  eventList: ScheduleMapper[];
  isSelected: any;
  daySelected: any;

  searching: boolean = true;

  loggedUser: UserMapper;

  constructor(public navCtrl: NavController, public navParams: NavParams, private alertCtrl: AlertController,
              private events: Events, private userPrv: User, private calendar: Calendar, private modalCtrl: ModalController,
              private schedulesPrv: SchedulesProvider) {
  }

  ionViewDidLoad() {
    this.date = new Date();
    this.daySelected = this.date.getDate();
    this.monthNames = ["Janeiro", "Fevereiro", "Março", "Abril", "Maio", "Junho", "Julho", "Agosto", "Setembro", "Outubro", "Novembro", "Dezembro"];
    this.getLoggedUser();
    this.getDaysOfMonth();
    this.loadEventsToday();
    console.log('ionViewDidLoad SchedulesPage');
  }

  async getLoggedUser() {
    this.loggedUser = await this.userPrv.user();
    this.events.publish(__USER_LOGGED, this.loggedUser);
  }

  getDaysOfMonth() {
    this.daysInThisMonth = new Array();
    this.daysInLastMonth = new Array();
    this.daysInNextMonth = new Array();
    this.currentMonth = this.monthNames[this.date.getMonth()];
    this.currentYear = this.date.getFullYear();
    if (this.date.getMonth() === new Date().getMonth()) {
      this.currentDate = new Date().getDate();
    } else {
      this.currentDate = 999;
    }

    var firstDayThisMonth = new Date(this.date.getFullYear(), this.date.getMonth(), 1).getDay();
    var prevNumOfDays = new Date(this.date.getFullYear(), this.date.getMonth(), 0).getDate();
    for (var i = prevNumOfDays - (firstDayThisMonth - 1); i <= prevNumOfDays; i++) {
      this.daysInLastMonth.push(i);
    }

    var thisNumOfDays = new Date(this.date.getFullYear(), this.date.getMonth() + 1, 0).getDate();
    for (var j = 0; j < thisNumOfDays; j++) {
      this.daysInThisMonth.push(j + 1);
    }

    var lastDayThisMonth = new Date(this.date.getFullYear(), this.date.getMonth() + 1, 0).getDay();
    // var nextNumOfDays = new Date(this.date.getFullYear(), this.date.getMonth()+2, 0).getDate();
    for (var k = 0; k < (6 - lastDayThisMonth); k++) {
      this.daysInNextMonth.push(k + 1);
    }
    var totalDays = this.daysInLastMonth.length + this.daysInThisMonth.length + this.daysInNextMonth.length;
    if (totalDays < 36) {
      for (var l = (7 - lastDayThisMonth); l < ((7 - lastDayThisMonth) + 7); l++) {
        this.daysInNextMonth.push(l);
      }
    }
  }

  goToLastMonth() {
    this.daySelected = 0;
    this.date = new Date(this.date.getFullYear(), this.date.getMonth(), 0);
    this.getDaysOfMonth();
  }

  goToNextMonth() {
    this.daySelected = 0;
    this.date = new Date(this.date.getFullYear(), this.date.getMonth() + 2, 0);
    this.getDaysOfMonth();
  }

  addEvent(event?: any) {
    if (!event) {
      let date = this.currentYear + '-' + (this.prepareMonth(this.date.getMonth())) + '-' + this.prepareDay(this.daySelected);
      event = { patient_id: '', scheduled_to: date, professional_id: '', patient: {name: ''}, professional: {name: ''}};
    }

    event = Object.assign({}, event);

    let evolutionDetailModal = this.modalCtrl.create(AddEventPage, {event: event});

    evolutionDetailModal.onDidDismiss(() => {
      this.loadEventsToday();
    });

    evolutionDetailModal.present();
  }

  openHospitalMonitoring() {
    this.navCtrl.push(HospitalMonitoringPage);
  }

  openEvolutions() {
    this.navCtrl.push(EvolutionsPage);
  }


  loadEventsToday() {
    this.eventList = new Array();
    var startDate = new Date(this.date.getFullYear(), this.date.getMonth(), this.date.getDate());

    this.searching = true;
    this.schedulesPrv.query(this.formatDate(startDate, true)).subscribe((data: ScheduleMapper[]) => {
      this.searching = false;
      this.eventList = data;
    });
  }

  checkEvent(day) {
    var date = this.date.getFullYear() + "-" + (this.prepareMonth()) + "-" + day;
    let eventsFound: any[] = [];
    if (this.eventList && this.eventList.length) {
      eventsFound = this.eventList.filter((item: ScheduleMapper) => {
        return item.scheduled_to.split(' ')[0] === date;
      });
    }
    return eventsFound.length;
  }

  selectDate(day) {
    this.daySelected = day;
    var thisDate1 = this.date.getFullYear() + "-" + (this.date.getMonth() + 1) + "-" + day;
    var thisDate2 = this.date.getFullYear() + "-" + (this.date.getMonth() + 1) + "-" + day;

    this.searching = true;
    this.schedulesPrv.query(thisDate1, thisDate2).subscribe((data: ScheduleMapper[]) => {
      this.searching = false;
      this.eventList = data;
    });
  }

  deleteEvent(evt) {
    let alert = this.alertCtrl.create({
      title: 'Confirm Delete',
      message: 'Are you sure want to delete this event?',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Ok',
          handler: () => {
            this.calendar.deleteEvent(evt.title, evt.location, evt.notes, new Date(evt.startDate.replace(/\s/, 'T')), new Date(evt.endDate.replace(/\s/, 'T'))).then(
              (msg) => {
                console.log(msg);
                this.loadEventsToday();
                this.selectDate(new Date(evt.startDate.replace(/\s/, 'T')).getDate());
              },
              (err) => {
                console.log(err);
              }
            )
          }
        }
      ]
    });
    alert.present();
  }

  formatDate(date: Date, hideTime?: boolean, twentyFourHours?: boolean) {

    let hours = '';
    if (!hideTime) {
      hours = '00:00:00';
      if (twentyFourHours) {
        hours = '23:59:59';
      }
    }

    let month: any = date.getMonth() + 1;

    if (month < 10) {
      month = '0' + month;
    }

    let day: any = date.getDate();

    if (day < 10) {
      day = '0' + day;
    }

    return (date.getFullYear() + "-" + month + "-" + day + " " + hours).trim();
  }

  prepareMonth(month?) {
    month = month ? month + 1 : this.date.getMonth() + 1;

    if (month < 10) {
      month = '0' + month;
    }
    return month;
  }

  prepareDay(day?) {
    if (day < 10) {
      day = '0' + day;
    }
    return day;
  }

  patientHouseholdMonitoring(event) {
    // if (event.attended_in_formatted) {
    //   return;
    // }
    this.navCtrl.push(PatientHouseholdMonitoringPage, {schedule: event});
  }
}



